import React from 'react'
import { Link } from 'gatsby'
import { Icon } from '../Icon'
import { Heading, Text } from '../../styles'
import { Spacer, Container, StyledCard, Content, Summary } from './style'
import { Card } from '@dhis2/ui-core'

const GuideCard = ({ title, summary, path }) => (
    <Spacer>
    <Link to={path}>
        <StyledCard>
            <Heading>{title}</Heading>
            <Summary>{summary}</Summary>
        </StyledCard>
    </Link>
    </Spacer>
)

export const GuideCards = ({ guides }) => (
    <Container>
        {guides.map(g => <GuideCard title={g.title} summary={g.summary} path={g.path}/>)}
    </Container>
)